/* src/assets/styles/About.css */
.about-container {
    padding: 2rem;
    max-width: 1200px;
    margin: 0 auto;
    margin-top: 80px;
}

.about-hero {
    background-image: url('../images/abt.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding: 4rem 2rem;
    text-align: center;
    margin-bottom: 2rem;
    color: white; /* Ensure text is readable against the background image */
    border-radius: 8px; /* Optional: for rounded corners */
}

.about-hero h1 {
    font-size: 2.5rem;
    margin: 0; /* Remove margin to avoid extra space around the heading */
}

.about-hero p {
    font-size: 1.2rem;
}

.about-content h2 {
    font-size: 2rem;
    color: #388E3C;
    margin-top: 1.5rem;
}

.about-content p {
    font-size: 1rem;
    line-height: 1.6;
    color: #333;
    margin-bottom: 1rem;
}

.about-content ul {
    list-style-type: none;
    padding: 0;
}

.about-content ul li {
    background: #f5f5f5;
    padding: 1rem;
    margin-bottom: 1rem;
    border-left: 5px solid #D32F2F;
}

.about-content ul li strong {
    color: #D32F2F;
}

.team-container {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    justify-content: center;
}

.team-member {
    background: #f5f5f5;
    padding: 1rem;
    text-align: center;
    border-radius: 8px;
    max-width: 200px;
}

.team-member img {
    width: 100%;
    border-radius: 50%;
    margin-bottom: 1rem;
}

.team-member h3 {
    font-size: 1.2rem;
    color: #D32F2F;
    margin-bottom: 0.5rem;
}

.team-member p {
    font-size: 1rem;
    color: #333;
}
